import React, { useState,useContext } from "react";
import Navbar from "../../components/Navbar/Navbar";
import "./Signin.css";
import {Link,useHistory} from 'react-router-dom'
import {firebaseContext} from '../../Store/Context'
import Footer from "../../components/Footer/Footer";

function Signin() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const {firebase} = useContext(firebaseContext)
  const history = useHistory()

  function handleSubmit(e){
    e.preventDefault()
    firebase.auth().signInWithEmailAndPassword(email,password).then(()=>{
      history.push('/')
    }).catch((error)=>{
      alert(error)
    }) 
  }

  return (
    <div className="all">
      <div class="upper">
        <Navbar />
        <div className="login-div">
          <form className="login" onSubmit={handleSubmit}>
            <h1>Sign In</h1>
            <div className="input-text">
              <input
                type="text"
                id="inputEmail"
                name="email"
                placeholder="Email "
                onfocus="inputOnFocus(this)"
                onblur="inputOnBlur(this)"
                value={email}
                onChange={(e)=>setEmail(e.target.value)}
              />
              <div className="warning-input" id="warningEmail">
                Please enter your valid email.
              </div>
            </div>
            <div className="input-text">
              <input
                type="password"
                id="inputPassword"
                name="password"
                placeholder="Password"
                onfocus="inputOnFocus(this)"
                onblur="inputOnBlur(this)"
                value={password}
                onChange={(e)=>setPassword(e.target.value)}
              />
              <div className="warning-input" id="warningPassword">
                Your password.
              </div>
            </div>
            <div>
              <button type="submit" className="signin-button">Sign In</button>
            </div>
            <div className="login-face"></div>
            <div className="new-members">
              New to Netflix?{" "}
              <Link href="#" className="signup-link" to="/">
                Sign up now
              </Link>
              .
            </div>
            <div className="protection color_link help"></div>
          </form>
        </div>
      </div>
      <Footer/>
    </div>
  );
}

export default Signin;
